#root{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
body{
    background-color: rgb(27, 25, 35);
    color: white;
}
.new{
    border-radius: 0.4rem;
    overflow: hidden;
}
.newInput{
    border: none;
    flex: 1 1 auto;
    padding-left: 0.5rem;
    background-color: transparent;
    cursor: text;
    outline: none;

}
.addLang{
    border: none;
    border-left: solid 1px black;
    padding: 0.5rem;
    font-weight: bold;
    color: #187b69;
    background-color: transparent;
    cursor: pointer;
    outline: none;
}
.newInput::placeholder{
    font-weight: bold;
}
.language{
    flex: 1;
    text-align: center;
    color: white;
    padding: 0.5rem;
    border-bottom: 1px #7c7c7c solid;
}
.word{
    flex: 1;
    text-align: center;
    color: white;
    padding: 0.5rem;
    border-bottom: 1px #7c7c7c solid;
}
.wordTranslation{
    flex: 1;
    text-align: center;
    color: white;
    background-color: #7c7c7c;
    padding: 0.5rem;
}
.translatedWord{
    display: none;
    flex: 1;
    text-align: center;
    color: white;
    padding: 0.5rem;
}
.addLang:hover{
    background-color: #187b69;
    color: white;
}
.main{
    width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.az{
    display: flex;
    width: 60%;
    justify-content: flex-end;
    align-items: center;
}
.lanWordRow{
    overflow: hidden;
    display: flex;
    flex-direction: row;
    position: relative;
}
.green{
    font-weight: bold;
    background-color: #079e07 !important;
}
.Container{
    width: 60%;
    min-height: 2.5rem;
    border-radius: 0.4rem;
    margin-top: 3rem;
    max-height: 80vh;
    overflow: auto;
}
.scroll-bar::-webkit-scrollbar {
    width: 4px;
  }
.x{
    position: absolute;
    right: 0;
    font-size: 1.2rem;
    height: 100%;
    border: none;
    width: 10%;
    font-weight: bold;
}
@media screen and (orientation:portrait){
    .Container{
        width:90%;
    }
}